.SearchBar {
  width: 65%;
  height: auto;
  margin: 15px auto;
  text-align: center;

  -moz-box-shadow: inset 0 0 4px #000000;
  -webkit-box-shadow: inset 0 0 4px #000000;
  box-shadow: inset 0 0 4px #000000;

  input {
    width: 90%;
    padding: 10px;
    text-align: left;
    background: #fff;
    -moz-box-shadow: inset 0 0 4px #000000;
    -webkit-box-shadow: inset 0 0 4px #000000;
    box-shadow: inset 0 0 4px #000000;
    border: 1px solid black;
  }

  button {
    padding: 10px;
    width: 10%;
    cursor: pointer;
    border: 1px solid black;
  }
}

@media (max-width: 795px) {
  .SearchBar {
    width: 75%;

    button {
      font-size: 11px;
      height: 100%;
      box-sizing: border-box;
    }
  }
}
